@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
}

p {
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

ul,
li {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;

  font-family: "Open Sans", sans-serif;
}

.site-container{position:absolute;width:100%;height:100%}.site-container .main-content{position:relative;display:flex;flex-direction:column;max-width:1120px;height:auto;left:50%;transform:translateX(-50%);padding-left:20px;padding-right:20px}.footer-container{position:relative;width:100%;height:500px;background:linear-gradient(93.66deg, #000 -20.82%, #4a4a4a 100.76%)}.footer-container .footer-content-container{display:flex;flex-direction:column}.footer-container .footer-content-container .link-container{position:relative;width:135px;left:50%;transform:translateX(-50%);margin-top:30px;margin-bottom:30px;text-align:center}.footer-container .footer-content-container .link-container .link-section-title{position:relative;padding-bottom:20px}.footer-container .footer-content-container .link-container .link-section-title>h4{font-size:24px;color:white;margin-bottom:10px}.footer-container .footer-content-container .link-container .link-section-title .footer-link>h5{font-size:1em;font-weight:400;color:white;padding-bottom:5px}.footer-container .footer-content-container .footer-subtitle-content{position:relative;width:100%}.footer-container .footer-content-container .footer-subtitle-content .footer-subtitle-container{position:relative;display:flex;flex-direction:column;text-align:center}.footer-container .footer-content-container .footer-subtitle-content .footer-subtitle-container .company-title{display:block;padding-bottom:30px}.footer-container .footer-content-container .footer-subtitle-content .footer-subtitle-container .company-title>h2{font-size:24px;color:white}.footer-container .footer-content-container .footer-subtitle-content .footer-subtitle-container .company-motto{display:block;padding-bottom:30px}.footer-container .footer-content-container .footer-subtitle-content .footer-subtitle-container .company-motto>h2{color:white;font-size:22px}.footer-container .footer-content-container .footer-subtitle-content .footer-subtitle-container .business-structure{display:block;padding-bottom:30px}.footer-container .footer-content-container .footer-subtitle-content .footer-subtitle-container .business-structure>h5{color:white;font-weight:300}@media screen and (max-width: 420px){.site-container .main-content{padding-left:0px;padding-right:0px}}

.navbar-container{position:fixed;width:100%;height:80px;background-color:#323232;z-index:9999}.navbar-container .navbar-content{display:flex;justify-content:space-between;padding-top:15px;padding-left:10px;padding-right:10px}.navbar-container .navbar-content .tce-logo{width:80px}.navbar-container .navbar-content .tce-logo>img{background-size:cover;width:100%}.navbar-container .navbar-content .company-name{margin-left:10px;display:inline-block;width:220px}.navbar-container .navbar-content .company-name>h1{color:white;font-size:16px}.navbar-container .navbar-content .nav-button{width:50px;height:50px}.navbar-container .navbar-content .nav-button:hover{cursor:pointer}.navbar-container .navbar-content .nav-button .gg-menu-boxed{left:50%;top:50%;color:white;transform:translate(-50%, -50%) scale(2.5);transform:translate(-50%, -50%) scale(var(--ggs, 2.5))}.mobile-menu-container{position:fixed;opacity:0;left:100%;width:175px;height:100vh;margin-top:80px;background-color:#3f3f3f;z-index:9999}.mobile-menu-container .mobile-nav-container{position:relative;width:calc(100%);margin-left:auto;height:300px}.mobile-menu-container .mobile-nav-container ul{text-align:center;padding-top:30px}.mobile-menu-container .mobile-nav-container ul>li{font-weight:bold;margin-bottom:1.3em;list-style:none;color:white}.mobile-menu-container .mobile-nav-container ul>li:hover{cursor:pointer}

.hero-container{position:relative;width:100%;max-width:420px;height:310px;left:0;margin-bottom:30px;margin-top:calc(80px)}.hero-container:after{content:"";position:absolute;width:100%;height:297px;left:0;top:0;background-color:#222222;opacity:25%;z-index:-1}.hero-container .hero-img-container{position:absolute;width:100%;z-index:-2}.hero-container .hero-img-container>img{background-size:cover;width:100%;height:300px}.hero-container .hero-content{position:relative;max-width:310px;height:260px;margin-left:20px;padding-top:20px}.hero-container .hero-content .title-container{display:flex;flex-direction:column;padding-bottom:20px}.hero-container .hero-content .title-container h2{margin-bottom:-10px;font-size:40px;color:white}.hero-container .hero-content .hero-subtitle{margin-top:5px}.hero-container .hero-content .hero-subtitle h4{font-weight:600;color:white}.hero-container .hero-content .cta-button-apply{width:165px;height:50px;background-color:red;margin-top:10px;border-radius:5px;text-align:center}.hero-container .hero-content .cta-button-apply:hover{cursor:pointer}.hero-container .hero-content .cta-button-apply h2{padding-top:5px;font-size:30px;font-weight:1000;color:white}

.info-card{position:relative;max-width:420px;height:435px;padding-left:20px;padding-right:20px;margin-bottom:20px}.info-card .card-title>h4{font-size:20px;font-weight:bold;padding-bottom:0.375em}.info-card .card-image{width:100%}.info-card .card-image>img{background-size:cover;width:100%}.info-card .card-body-text{min-height:300px}

.mission-container{position:relative;height:675px;max-width:420px;background-color:#3c3c3c;padding-top:20px;padding-left:20px;padding-right:20px}.mission-container .mission-image-container{position:relative;width:100%;height:330px}.mission-container .mission-image-container .mission-title-container{position:absolute;width:100%;top:50%;transform:translateY(-50%);text-align:center}.mission-container .mission-image-container .mission-title-container:before{content:"";position:absolute;width:100%;height:60px;left:0;top:inherit;transform:inherit;background-color:#222222;opacity:70%;z-index:-1}.mission-container .mission-image-container .mission-title-container>h2{font-size:3em;color:white;padding:0;margin:0}.mission-container .mission-image-container>img{background-size:cover;width:100%}.mission-container .mission-body-text-container{width:100%;height:auto}.mission-container .mission-body-text-container>p{text-align:center;font-weight:500;line-height:150%;color:white}

.cta-apply-container{position:relative;text-align:center;margin-top:20px;max-width:420px;padding-left:20px;padding-right:20px;padding-top:30px;padding-bottom:30px;margin-bottom:40px}.cta-apply-container .cta-button-apply{position:relative;width:165px;height:60px;left:50%;transform:translateX(-50%);background-color:red;border-radius:5px;text-align:center}.cta-apply-container .cta-button-apply:hover{cursor:pointer}.cta-apply-container .cta-button-apply>h2{padding-top:10px;font-size:30px;font-weight:1000;color:white}

