.hero-container {
  position: relative;
  width: 100%;
  max-width: 420px;
  height: 310px;
  //   background-color: lightgreen;
  left: 0;
  margin-bottom: 30px;
  margin-top: calc(80px);

  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 297px;
    left: 0;
    top: 0;

    background-color: #222222;
    opacity: 25%;
    z-index: -1;
  }

  .hero-img-container {
    position: absolute;
    width: 100%;
    z-index: -2;
    > img {
      background-size: cover;
      width: 100%;
      height: 300px;
    }
  }

  .hero-content {
    position: relative;
    max-width: 310px;
    height: 260px;
    // background-color: crimson;
    margin-left: 20px;
    padding-top: 20px;

    .title-container {
      display: flex;
      flex-direction: column;
      padding-bottom: 20px;
      // background-color: lightpink;

      // .title-box {
      //   display: block;
      // }

      h2 {
        // height: 20px;
        margin-bottom: -10px;
        font-size: 40px;
        color: white;
      }
    }

    .hero-subtitle {
      margin-top: 5px;
      h4 {
        font-weight: 600;
        color: white;
      }
    }

    .cta-button-apply {
      width: 165px;
      height: 50px;
      background-color: red;
      margin-top: 10px;
      border-radius: 5px;
      text-align: center;

      &:hover {
        cursor: pointer;
      }

      h2 {
        padding-top: 5px;
        font-size: 30px;
        font-weight: 1000;
        color: white;
      }
    }
  }
}
